<template>
 <div>
    <cms-breadcrumb to="/comment/list" type='list'>
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-list-layout">
    <!-- 数据列表 -->
    <table class="comment-table" v-loading="loading">
      <tbody>
        <tr class="comment-th">
          <td style="padding-left:10px;">
            <div v-if="!loading&&formData.order">
              <span class="comment-status" >
                {{formData.order.commentStatus==2?'已评价':'双方已评'}}
              </span>
              <span>{{formData.orderCode}}</span>
            </div>
          </td>
          <td class="comment-time">
            <span style="margin-left:10px;">商品评价</span>
             <p>
              <span style="margin-right:80px">评价人：{{formData.memberName}}</span>
              <span style="margin-right:10px;">评论时间：{{$moment(formData.createTime)}}</span>
            </p>
          </td>
        </tr>
        <tr style="border:1px solid #EBEEF5;"  v-for="(info,indexs) in formData.commentdList" :key="indexs">
          <td class="comment-goods">
            <!-- 商品信息 -->
            <div class="comment__inner">
              <img class="product-img" :src="$cms.filterUrl(info.product.coverImage)">
              <div class="product-title">
                <h3 class="comment-title-h">{{info.product.name}}</h3>
                <p class="title-p" style="max-width: 340px;" v-if="info.productSkuName">
                  规格:{{info.productSkuName}}</p>
              </div>
            </div>
         </td>
         <td class="comment-item">
           <!-- 商品评价 -->
           <div class="comment-remark"  style="display:flex;"
            :style="{'border-bottom':formData.order.commentStatus==4?'1px solid #EBEEF5':'0'}">
             <div style="width:50%">
               <el-rate v-model="info.matchV" style="margin-bottom:5px;" :show-text="true" class="starts-rate"
                :texts="starsText">
               </el-rate>
               <el-input type="textarea" v-model="info.content" :rows="5" style="resize:none;"
                placeholder="请填写您对买家的回复吧,不超过500字" maxlength="500">
               </el-input>
             </div>
             <div style="width:50%">
               <div class="comment-imgs">
                  <thumb-img :data="info.spaceDataList"></thumb-img>
               </div>
             </div>
           </div>
          <div class="comment-remark" v-if="formData.order.commentStatus==4">
             <div style="width:50%">
               <p style="margin-bottom:5px;">
                 {{formData.store.storeName}}&nbsp;回复:
                </p>
               <el-input type="textarea" v-model="info.storeContent" :rows="5"
                 placeholder="请填写您对商家的回复吧,不超过500字" maxlength="500">
               </el-input>
             </div>
           </div>
         </td>
        </tr>
        <!-- 总体评价 -->
        <tr class="comment-all">
          <td colspan="2">
            <div style="display: flex;margin-left:10px;align-items:center;">
              <span>店铺评价</span>
              <div class="comment-stars">
                <label class="stars-label">商品符合度:</label>
                <el-rate v-model="formData.matchV" :show-text="true" class="starts-rate"
                :texts="starsText">
                </el-rate>
                <label class="stars-label">发货速度:</label>
                <el-rate v-model="formData.express" :show-text="true" class="starts-rate"
                 :texts="starsText">
                </el-rate>
                <label class="stars-label">服务态度:</label>
                <el-rate v-model="formData.service" :show-text="true" class="starts-rate"
                 :texts="starsText">
                </el-rate>
              </div>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
    <div style="text-align:right;margin-top:30px;width:48%;">
      <cms-button size="small" :colorPicker='true' @click.stop="update">
        修改评论
      </cms-button>
    </div>
  </div>
 </div>
</template>

<script>
import form from '@/mixins/form'
import { commentGet, commentUpdate } from '@/api/marketing/index.js'
import thumbImg from './thumbImg'
export default {
  mixins: [form],
  components: {
    thumbImg
  },
  data() {
    return {
      formData: {},
      loading: false,
      starsText: ['差评', '差评', '中评', '好评', '好评']
    }
  },
  methods: {
    getFormData(id) {
      this.loading = true
      commentGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
        } else {
          this.$errorMessage(res.message)
        }
        this.loading = false
      })
    },
    update() {
      let flag = true// 验证买家评论的必填
      const commentdList = []
      this.formData.commentdList.forEach(v => { // 循环评论列表
        const temObj = {
          id: v.id,
          matchV: v.matchV,
          content: v.content,
          storeContent: v.storeContent,
          spaceDataIds: []
        }
        if (v.content === '' || /^\s/.test(v.content)) { // 不能为空，且不能以空格开头
          flag = false
        }
        v.spaceDataList.forEach(k => { // 循环评论列表的图片
          temObj.spaceDataIds.push(k.id)
        })
        commentdList.push(temObj)
      })
      const params = {
        id: this.formData.id,
        express: this.formData.express,
        matchV: this.formData.matchV,
        service: this.formData.service,
        commentdList: commentdList
      }
      if (flag) {
        commentUpdate(params).then(res => {
          if (res.code === 200) {
            this.$successMessage('修改成功')
            this.$routerLink('/comment/list', 'list')
          } else {
            this.$errorMessage(res.message)
          }
        })
      } else {
        this.$errorMessage('请输入买家评论消息')
      }
    }
  },
  created() {
    this.getFormData(this.$route.query.id)
  }
}
</script>
<style scoped>
.comment-table{
  width: 100%;
  border: 1px solid #EBEEF5;
  padding: 0 10px;
  color: rgb(96, 98, 102);
  border-collapse: collapse;
  font-size: 12px;
}
.comment-th{
  background-color: #f7f7f7;
  text-align: left;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  font-weight: normal;
}
.comment-goods{
  width: 40%;
  padding: 10px 0;
  border-right:1px solid #EBEEF5;
  vertical-align: top;
}
.comment-item{
  width: 60%;
  vertical-align: top;
}
.comment-title{
  font-size: 12px;
  max-width: 360px;
  overflow-wrap: break-word;
}
.comment-all{
  height: 40px;
}
</style>




